import {
    CheckCircle2,
    ChevronDown,
    ChevronRight,
    Circle,
    Download,
    Eye,
    EyeOff,
    Folder,
    Layers,
} from 'lucide-react';
import { useState } from 'react';
import { LayerGroup } from '../types';

interface LayerPanelProps {
    groups: LayerGroup[];
    toggleLayer: (groupId: string, layerId: string) => void;
    updateOpacity: (groupId: string, layerId: string, opacity: number) => void;
}

const LayerPanel: React.FC<LayerPanelProps> = ({
    groups,
    toggleLayer,
    updateOpacity,
}) => {
    // STATE INITIALIZATION YANG DINAMIS
    // Logic: Jika ID group adalah 'group-basemap', set false (tertutup).
    // Selain itu set true (terbuka).
    const [expandedGroups, setExpandedGroups] = useState<
        Record<string, boolean>
    >(() => {
        const initialState: Record<string, boolean> = {};
        groups.forEach((group) => {
            initialState[group.id] = group.id !== 'group-basemap';
        });
        return initialState;
    });

    const toggleGroup = (id: string) => {
        setExpandedGroups((prev) => ({ ...prev, [id]: !prev[id] }));
    };

    return (
        <div className="h-full w-80 bg-slate-900/95 backdrop-blur-md border-r border-slate-700 flex flex-col shadow-2xl animate-in slide-in-from-left duration-300 z-50">
            {/* Header Panel */}
            <div className="p-5 border-b border-slate-700 bg-slate-900 shadow-sm">
                <h2 className="text-lg font-bold text-primary-400 tracking-tight flex items-center gap-2">
                    <Layers size={20} className="text-primary-500" />
                    DATA LAYERS
                </h2>
                <p className="text-xs text-slate-500 mt-1">
                    Manage spatial data visibility
                </p>
            </div>

            {/* Scrollable List */}
            <div className="flex-1 overflow-y-auto p-4 space-y-4 scrollbar-thin scrollbar-thumb-slate-700 scrollbar-track-transparent">
                {groups.map((group) => {
                    const isBaseMap = group.id === 'group-basemap';
                    const isExpanded = expandedGroups[group.id];

                    return (
                        <div
                            key={group.id}
                            className={`border transition-all duration-200 rounded-lg overflow-hidden ${
                                isExpanded
                                    ? 'border-slate-600 bg-slate-800/40'
                                    : 'border-slate-800 bg-slate-900/40'
                            }`}
                        >
                            {/* Group Header */}
                            <button
                                onClick={() => toggleGroup(group.id)}
                                className="w-full flex items-center justify-between p-3 bg-slate-800/80 hover:bg-slate-750 transition-colors cursor-pointer group"
                            >
                                <div className="flex items-center gap-2">
                                    <Folder
                                        size={16}
                                        className={`${
                                            isBaseMap
                                                ? 'text-slate-500'
                                                : 'text-primary-400 group-hover:text-primary-300'
                                        }`}
                                    />
                                    <span className="font-semibold text-sm text-slate-200 group-hover:text-white">
                                        {group.name}
                                    </span>
                                </div>
                                {isExpanded ? (
                                    <ChevronDown
                                        size={16}
                                        className="text-slate-400"
                                    />
                                ) : (
                                    <ChevronRight
                                        size={16}
                                        className="text-slate-500"
                                    />
                                )}
                            </button>

                            {/* Layer List */}
                            {isExpanded && (
                                <div className="p-3 space-y-3 border-t border-slate-700/50">
                                    {group.layers.map((layer) => (
                                        <div
                                            key={layer.id}
                                            className="space-y-2 animate-in fade-in duration-300"
                                        >
                                            <div className="flex items-center justify-between group/layer">
                                                <div className="flex items-center gap-3">
                                                    <button
                                                        onClick={() =>
                                                            toggleLayer(
                                                                group.id,
                                                                layer.id
                                                            )
                                                        }
                                                        className={`transition-colors focus:outline-none ${
                                                            layer.visible
                                                                ? 'text-primary-400 hover:text-primary-300'
                                                                : 'text-slate-600 hover:text-slate-500'
                                                        }`}
                                                        title={
                                                            isBaseMap
                                                                ? 'Select Base Map'
                                                                : 'Toggle Visibility'
                                                        }
                                                    >
                                                        {isBaseMap ? (
                                                            layer.visible ? (
                                                                <CheckCircle2
                                                                    size={18}
                                                                />
                                                            ) : (
                                                                <Circle
                                                                    size={18}
                                                                />
                                                            )
                                                        ) : layer.visible ? (
                                                            <Eye size={18} />
                                                        ) : (
                                                            <EyeOff size={18} />
                                                        )}
                                                    </button>
                                                    <span
                                                        className={`text-sm cursor-pointer select-none transition-colors ${
                                                            layer.visible
                                                                ? 'text-slate-200 font-medium'
                                                                : 'text-slate-500'
                                                        }`}
                                                        onClick={() =>
                                                            toggleLayer(
                                                                group.id,
                                                                layer.id
                                                            )
                                                        }
                                                    >
                                                        {layer.name}
                                                    </span>
                                                </div>

                                                <div className="flex items-center gap-2">
                                                    {/* Color Indicator */}
                                                    {layer.type === 'vector' &&
                                                        layer.color && (
                                                            <div
                                                                className="w-2.5 h-2.5 rounded-full ring-1 ring-slate-700/50"
                                                                style={{
                                                                    backgroundColor:
                                                                        layer.color,
                                                                }}
                                                            />
                                                        )}

                                                    {/* Download Button */}
                                                    {layer.apiKey && (
                                                        <a
                                                            href={`/api/export/${layer.apiKey}`}
                                                            download
                                                            className="opacity-0 group-hover/layer:opacity-100 p-1 text-slate-500 hover:text-primary-400 hover:bg-slate-700 rounded transition-all"
                                                            title="Download GeoJSON"
                                                        >
                                                            <Download
                                                                size={14}
                                                            />
                                                        </a>
                                                    )}
                                                </div>
                                            </div>

                                            {/* Opacity Slider */}
                                            {layer.visible && (
                                                <div className="flex items-center gap-3 pl-8 pr-1 animate-in slide-in-from-top-1 duration-200">
                                                    <input
                                                        type="range"
                                                        min="0"
                                                        max="1"
                                                        step="0.1"
                                                        value={layer.opacity}
                                                        onChange={(e) =>
                                                            updateOpacity(
                                                                group.id,
                                                                layer.id,
                                                                parseFloat(
                                                                    e.target
                                                                        .value
                                                                )
                                                            )
                                                        }
                                                        className="flex-1 h-1.5 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-primary-500 hover:accent-primary-400 focus:outline-none focus:ring-1 focus:ring-primary-500/50"
                                                    />
                                                    <span className="text-[10px] text-slate-400 w-6 text-right font-mono">
                                                        {Math.round(
                                                            layer.opacity * 100
                                                        )}
                                                        %
                                                    </span>
                                                </div>
                                            )}
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default LayerPanel;
